@media screen and (min-width: 1000px) {
  .hidden {
    opacity: 0;
  }
  
  @keyframes fadeInDown {
    0% {
      opacity: 0;
      transform: translateY(100px);
    }
  
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .fade-in-element {
    animation: fadeInDown 2s;
  }
}
